import React, { useState, useEffect, useRef } from 'react';
import { settingsService } from '../services/api';

const navItems = [
  { label: 'Dashboard', icon: (
    <svg className="w-6 h-6" fill="none" stroke="currentColor" strokeWidth="2" viewBox="0 0 24 24"><path strokeLinecap="round" strokeLinejoin="round" d="M3 12l2-2m0 0l7-7 7 7M13 5v6h6" /></svg>
  ), link: '/' },
  { label: 'User Stories', icon: (
    <svg className="w-6 h-6" fill="none" stroke="currentColor" strokeWidth="2" viewBox="0 0 24 24"><path strokeLinecap="round" strokeLinejoin="round" d="M8 17l4 4 4-4m0-5V3a1 1 0 00-1-1H7a1 1 0 00-1 1v9m0 0l4 4 4-4" /></svg>
  ), link: '#' },
  { label: 'Activity Logs', icon: (
    <svg className="w-6 h-6" fill="none" stroke="currentColor" strokeWidth="2" viewBox="0 0 24 24"><path strokeLinecap="round" strokeLinejoin="round" d="M9 17v-6a2 2 0 012-2h2a2 2 0 012 2v6m-6 0h6" /></svg>
  ), link: '/activitylogs' },
  { label: 'Automation', icon: (
    <svg className="w-6 h-6" fill="none" stroke="currentColor" strokeWidth="2" viewBox="0 0 24 24"><path strokeLinecap="round" strokeLinejoin="round" d="M12 8v4l3 3" /></svg>
  ), link: '#' },
  { label: 'Settings', icon: (
    <svg className="w-6 h-6" fill="none" stroke="currentColor" strokeWidth="2" viewBox="0 0 24 24"><path strokeLinecap="round" strokeLinejoin="round" d="M12 4v16m8-8H4" /></svg>
  ), link: '/settings' },
];

const AI_SERVICES = [
  { label: 'Zap AI', value: 'openai' },
  { label: 'My AI', value: 'gemini' },
];

const TEST_CASE_FORMATS = [
  { label: 'Gherkin', value: 'gherkin' },
  { label: 'Plain Text', value: 'plain' },
];

const SIDEBAR_OPTIONS = [
  { label: 'Profile', value: 'profile' },
  { label: 'Application', value: 'app' },
];

// Tooltip component for info icons
const InfoTooltip = ({ message }) => (
  <span className="relative group inline-block ml-2 align-middle">
    <svg className="w-5 h-5 text-white/80 group-hover:text-white cursor-pointer inline" fill="currentColor" viewBox="0 0 20 20">
      <path d="M18 10A8 8 0 11 2 10a8 8 0 0116 0zm-8-3a1 1 0 100-2 1 1 0 000 2zm2 7a1 1 0 10-2 0v-4a1 1 0 112 0v4z" />
    </svg>
    <span className="absolute left-1/2 -translate-x-1/2 mt-2 w-64 bg-gray-900 text-white text-xs rounded-lg px-3 py-2 opacity-0 group-hover:opacity-100 transition-opacity z-10 pointer-events-none shadow-lg">
      {message}
    </span>
  </span>
);

const Settings = () => {
  const [activeTab, setActiveTab] = useState('profile');
  const [settings, setSettings] = useState({
    aiService: 'openai',
    openai: {
      url: '',
      apiKey: '',
      model: 'gpt-4',
    },
    automation: {
      projectLocation: '',
      scriptLanguage: 'java',
    },
    jira: {
      url: '',
      username: '',
      apiToken: '',
      projectId: '',
      bugLinkType: '',
      testCaseLinkType: '',
      testCaseIssueType: '',
    },
    testCaseFormat: 'gherkin',
    profile: {
      firstName: '',
      lastName: '',
      email: '',
      avatar: '',
    },
    integration: 'jira',
  });
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [success, setSuccess] = useState(null);
  const [testing, setTesting] = useState(false);
  const [avatarPreview, setAvatarPreview] = useState('');
  const fileInputRef = useRef();

  useEffect(() => {
    fetchSettings();
  }, []);

  const fetchSettings = async () => {
    try {
      setLoading(true);
      const response = await settingsService.get();
      setSettings({ ...settings, ...response.data });
      setError(null);
    } catch (err) {
      setError('Failed to fetch settings');
      console.error('Error fetching settings:', err);
    } finally {
      setLoading(false);
    }
  };

  const handleInputChange = (section, field, value) => {
    setSettings({
      ...settings,
      [section]: {
        ...settings[section],
        [field]: value,
      },
    });
  };

  const handleRootChange = (field, value) => {
    setSettings({
      ...settings,
      [field]: value,
    });
  };

  const handleAvatarChange = (e) => {
    const file = e.target.files[0];
    if (file) {
      const reader = new FileReader();
      reader.onloadend = () => {
        setAvatarPreview(reader.result);
        setSettings({
          ...settings,
          profile: {
            ...settings.profile,
            avatar: reader.result,
          },
        });
      };
      reader.readAsDataURL(file);
    }
  };

  const handleAvatarDelete = () => {
    setAvatarPreview('');
    setSettings({
      ...settings,
      profile: {
        ...settings.profile,
        avatar: '',
      },
    });
  };

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      setError(null);
      setSuccess(null);
      await settingsService.update(settings);
      setSuccess('Settings saved successfully');
    } catch (err) {
      setError('Failed to save settings');
      console.error('Error saving settings:', err);
    }
  };

  const handleTestJira = async () => {
    try {
      setTesting(true);
      setError(null);
      setSuccess(null);
      await settingsService.testJiraConnection(settings.jira);
      setSuccess('Jira connection successful');
    } catch (err) {
      setError('Failed to connect to Jira');
      console.error('Error testing Jira connection:', err);
    } finally {
      setTesting(false);
    }
  };

  if (loading) {
    return (
      <div className="flex items-center justify-center h-64">
        <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-primary-600"></div>
      </div>
    );
  }

  return (
    <div className="w-full flex flex-row justify-start items-start gap-x-2 py-10 px-4">
      {/* Local sidebar for Profile/Application */}
      <aside className="w-44 flex-shrink-0 pr-2">
        <h1 className="text-3xl font-bold mb-4">Settings</h1>
        <nav className="flex flex-col gap-2">
          {SIDEBAR_OPTIONS.map((opt) => (
            <button
              key={opt.value}
              className={`text-left px-4 py-2 rounded-lg font-medium transition-colors ${activeTab === opt.value ? 'bg-primary-100 text-primary-700' : 'text-gray-700 hover:bg-gray-100'}`}
              onClick={() => setActiveTab(opt.value)}
            >
              {opt.label}
            </button>
          ))}
        </nav>
      </aside>
      {/* Main Content */}
      <main className="flex-1 flex flex-col items-start justify-start">
        <form onSubmit={handleSubmit} className="w-full max-w-xl">
          {/* Horizontal Tabs */}
          <div className="border-b border-gray-200 mb-8">
            <nav className="-mb-px flex space-x-8" aria-label="Tabs">
              {SIDEBAR_OPTIONS.map((tab) => (
                <button
                  key={tab.value}
                  className={`whitespace-nowrap py-4 px-1 border-b-2 font-medium text-sm ${
                    activeTab === tab.value
                      ? 'border-primary-600 text-primary-700'
                      : 'border-transparent text-gray-500 hover:text-gray-700 hover:border-gray-300'
                  }`}
                  onClick={() => setActiveTab(tab.value)}
                  type="button"
                >
                  {tab.label}
                </button>
              ))}
            </nav>
          </div>
          {activeTab === 'profile' && (
            <div className="bg-white rounded-2xl shadow p-8 mb-8">
              <h2 className="text-2xl font-bold mb-6">Profile Settings</h2>
              <div className="flex flex-col md:flex-row gap-8 items-center mb-8">
                <div className="flex flex-col items-center">
                  <div className="relative w-28 h-28 mb-2">
                    <img
                      src={avatarPreview || settings.profile.avatar || '/images/avatar.gif'}
                      alt="Avatar"
                      className="w-28 h-28 rounded-full object-cover border-4 border-primary-200 shadow"
                    />
                    <input
                      type="file"
                      accept="image/*"
                      ref={fileInputRef}
                      className="hidden"
                      onChange={handleAvatarChange}
                    />
                    <button
                      type="button"
                      className="absolute bottom-2 right-2 bg-primary-600 text-white rounded-full p-2 shadow hover:bg-primary-700"
                      onClick={() => fileInputRef.current.click()}
                      title="Upload New"
                    >
                      <svg className="w-5 h-5" fill="none" stroke="currentColor" strokeWidth="2" viewBox="0 0 24 24"><path strokeLinecap="round" strokeLinejoin="round" d="M12 4v16m8-8H4" /></svg>
                    </button>
                  </div>
                  <button
                    type="button"
                    className="text-xs text-gray-500 hover:text-red-600"
                    onClick={handleAvatarDelete}
                  >
                    Delete avatar
                  </button>
                </div>
                <div className="flex-1 grid grid-cols-1 md:grid-cols-2 gap-6">
                  <div>
                    <label className="block text-sm font-medium text-gray-700">First Name</label>
                    <input
                      type="text"
                      value={settings.profile.firstName}
                      onChange={e => handleInputChange('profile', 'firstName', e.target.value)}
                      className="input mt-1 w-full"
                      placeholder="First name"
                    />
                  </div>
                  <div>
                    <label className="block text-sm font-medium text-gray-700">Last Name</label>
                    <input
                      type="text"
                      value={settings.profile.lastName}
                      onChange={e => handleInputChange('profile', 'lastName', e.target.value)}
                      className="input mt-1 w-full"
                      placeholder="Last name"
                    />
                  </div>
                  <div>
                    <label className="block text-sm font-medium text-gray-700">Email</label>
                    <input
                      type="email"
                      value={settings.profile.email}
                      onChange={e => handleInputChange('profile', 'email', e.target.value)}
                      className="input mt-1 w-full"
                      placeholder="your@email.com"
                    />
                  </div>
                </div>
              </div>
              <div className="flex justify-end">
                <button type="submit" className="btn-primary px-8 py-2">Save Changes</button>
              </div>
            </div>
          )}
          {activeTab === 'app' && (
            <div className="bg-white rounded-xl shadow p-8">
              <h2 className="text-2xl font-bold mb-8 text-center">Application Settings</h2>
              {/* AI Service Selection */}
              <div className="flex gap-4 mb-6">
                {AI_SERVICES.map((s) => (
                  <button
                    key={s.value}
                    type="button"
                    className={`px-8 py-2 rounded-full font-semibold text-lg shadow-sm border transition-colors focus:outline-none ${
                      settings.aiService === s.value
                        ? (s.value === 'openai'
                            ? 'bg-blue-400 text-white'
                            : 'bg-cyan-300 text-gray-900')
                        : 'bg-white text-gray-700 border-gray-300 hover:bg-gray-100'
                    }`}
                    onClick={() => handleRootChange('aiService', s.value)}
                  >
                    {s.label}
                  </button>
                ))}
              </div>
              <div className="grid grid-cols-1 gap-y-4 mb-8">
                <label className="font-medium flex flex-col gap-1">AI API URL*:
                  <input type="text" value={settings.openai.url} onChange={e => handleInputChange('openai', 'url', e.target.value)} className="bg-gray-100 border-0 rounded-md px-3 py-2 focus:ring-2 focus:ring-blue-200" placeholder="https://api.openai.com/v1/chat/completions" disabled={settings.aiService === 'openai'} />
                </label>
                <label className="font-medium flex flex-col gap-1">API Key*:
                  <input type="password" value={settings.openai.apiKey} onChange={e => handleInputChange('openai', 'apiKey', e.target.value)} className="bg-gray-100 border-0 rounded-md px-3 py-2 focus:ring-2 focus:ring-blue-200" placeholder="Your OpenAI API key" disabled={settings.aiService === 'openai'} />
                </label>
                <label className="font-medium flex flex-col gap-1">AI Model:
                  <input type="text" value={settings.openai.model} onChange={e => handleInputChange('openai', 'model', e.target.value)} className="bg-gray-100 border-0 rounded-md px-3 py-2 focus:ring-2 focus:ring-blue-200" placeholder="gpt-4o" disabled={settings.aiService === 'openai'} />
                </label>
              </div>

              {/* Automation Project Details */}
              <div className="flex items-center mb-4">
                <div className="font-bold text-lg bg-blue-400 text-white rounded-full px-6 py-2 border border-blue-700 flex items-center">
                  Automation Project Details
                  <InfoTooltip message="Add your automation project local location & preferred scripting programming language." />
                </div>
              </div>
              <div className="grid grid-cols-1 gap-y-4 mb-8">
                <label className="font-medium flex flex-col gap-1">Project Location*:
                  <input type="text" value={settings.automation.projectLocation} onChange={e => handleInputChange('automation', 'projectLocation', e.target.value)} className="bg-gray-100 border-0 rounded-md px-3 py-2 focus:ring-2 focus:ring-blue-200" placeholder="/path/to/your/project" />
                </label>
                <label className="font-medium flex flex-col gap-1">Script Language*:
                  <input type="text" value={settings.automation.scriptLanguage} onChange={e => handleInputChange('automation', 'scriptLanguage', e.target.value)} className="bg-gray-100 border-0 rounded-md px-3 py-2 focus:ring-2 focus:ring-blue-200" placeholder="java" />
                </label>
              </div>

              {/* Project Management Tool */}
              <div className="flex items-center mb-4">
                <div className="font-bold text-lg bg-blue-400 text-white rounded-full px-6 py-2 border border-blue-700 flex items-center">
                  Project Management Tool
                  <InfoTooltip message="Add your project details." />
                </div>
              </div>
              <div className="grid grid-cols-2 gap-x-8 gap-y-4 mb-8">
                <label className="font-bold flex flex-col gap-1">Project ID*:
                  <input type="text" value={settings.jira.projectId} onChange={e => handleInputChange('jira', 'projectId', e.target.value)} className="bg-gray-100 border-0 rounded-md px-3 py-2 focus:ring-2 focus:ring-blue-200" placeholder="scrum" />
                </label>
                <label className="font-bold flex flex-col gap-1">Bug Link Type:
                  <input type="text" value={settings.jira.bugLinkType} onChange={e => handleInputChange('jira', 'bugLinkType', e.target.value)} className="bg-gray-100 border-0 rounded-md px-3 py-2 focus:ring-2 focus:ring-blue-200" placeholder="Blocks" />
                </label>
                <label className="font-bold flex flex-col gap-1">TC Link Type:
                  <input type="text" value={settings.jira.testCaseLinkType} onChange={e => handleInputChange('jira', 'testCaseLinkType', e.target.value)} className="bg-gray-100 border-0 rounded-md px-3 py-2 focus:ring-2 focus:ring-blue-200" placeholder="Relates" />
                </label>
                <label className="font-bold flex flex-col gap-1">TC Issue Type:
                  <input type="text" value={settings.jira.testCaseIssueType} onChange={e => handleInputChange('jira', 'testCaseIssueType', e.target.value)} className="bg-gray-100 border-0 rounded-md px-3 py-2 focus:ring-2 focus:ring-blue-200" placeholder="Task" />
                </label>
                <label className="font-bold flex flex-col gap-1 col-span-2">URL*:
                  <input type="text" value={settings.jira.url} onChange={e => handleInputChange('jira', 'url', e.target.value)} className="bg-gray-100 border-0 rounded-md px-3 py-2 focus:ring-2 focus:ring-blue-200" placeholder="https://your-domain.atlassian.net" />
                </label>
                <label className="font-bold flex flex-col gap-1 col-span-2">API Token*:
                  <input type="password" value={settings.jira.apiToken} onChange={e => handleInputChange('jira', 'apiToken', e.target.value)} className="bg-gray-100 border-0 rounded-md px-3 py-2 focus:ring-2 focus:ring-blue-200" placeholder="Your Jira API token" />
                </label>
                <label className="font-bold flex flex-col gap-1 col-span-2">User Name:
                  <input type="email" value={settings.jira.username} onChange={e => handleInputChange('jira', 'username', e.target.value)} className="bg-gray-100 border-0 rounded-md px-3 py-2 focus:ring-2 focus:ring-blue-200" placeholder="your-email@example.com" />
                </label>
              </div>

              {/* Test Case Format */}
              <div className="flex items-center gap-4 mb-8">
                <span className="px-6 py-2 rounded-full font-semibold text-lg bg-blue-400 text-white border border-blue-700 flex items-center">
                  Test Case Format
                  <InfoTooltip message="You can create test cases in Gherkin or in simple English format." />
                </span>
                <select value={settings.testCaseFormat} onChange={e => handleRootChange('testCaseFormat', e.target.value)} className="bg-gray-200 border-0 rounded-md px-6 py-2 font-semibold text-lg">
                  {TEST_CASE_FORMATS.map((f) => (
                    <option key={f.value} value={f.value}>{f.label}</option>
                  ))}
                </select>
              </div>

              {/* Save/Cancel Buttons */}
              <div className="flex justify-center gap-8 mt-8">
                <button type="submit" className="px-16 py-3 rounded-full bg-teal-100 text-teal-900 font-bold text-2xl shadow hover:bg-teal-200">Save</button>
                <button type="button" className="px-16 py-3 rounded-full bg-teal-100 text-teal-900 font-bold text-2xl shadow hover:bg-teal-200">Cancel</button>
              </div>
            </div>
          )}
          {error && (
            <div className="bg-red-100 border border-red-400 text-red-700 px-4 py-3 rounded mt-4">
              {error}
            </div>
          )}
          {success && (
            <div className="bg-green-100 border border-green-400 text-green-700 px-4 py-3 rounded mt-4">
              {success}
            </div>
          )}
        </form>
      </main>
    </div>
  );
};

export default Settings; 