import React, { useState, useEffect, useRef } from 'react';
import { testCaseService } from '../services/api';

const ActivityLogs = () => {
  const [testCases, setTestCases] = useState([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState(null);
  const [userStory, setUserStory] = useState('');
  const [generating, setGenerating] = useState(false);
  const [filterOpen, setFilterOpen] = useState(false);
  const [filters, setFilters] = useState({
    testCase: true,
    bugs: true,
    userStory: true,
    automation: true,
  });
  const filterRef = useRef();
  const [selected, setSelected] = useState([]);

  useEffect(() => {
    fetchTestCases();
  }, []);

  useEffect(() => {
    function handleClickOutside(event) {
      if (filterRef.current && !filterRef.current.contains(event.target)) {
        setFilterOpen(false);
      }
    }
    if (filterOpen) {
      document.addEventListener('mousedown', handleClickOutside);
    } else {
      document.removeEventListener('mousedown', handleClickOutside);
    }
    return () => {
      document.removeEventListener('mousedown', handleClickOutside);
    };
  }, [filterOpen]);

  const handleFilterChange = (name) => {
    setFilters((prev) => ({ ...prev, [name]: !prev[name] }));
  };

  const fetchTestCases = async () => {
    try {
      setLoading(true);
      const response = await testCaseService.getAll();
      setTestCases(response.data);
      setError(null);
    } catch (err) {
      setError('Failed to fetch test cases');
      console.error('Error fetching test cases:', err);
    } finally {
      setLoading(false);
    }
  };

  const handleGenerateTestCase = async (e) => {
    e.preventDefault();
    if (!userStory.trim()) return;

    try {
      setGenerating(true);
      const response = await testCaseService.generate(userStory);
      setTestCases([...testCases, response.data]);
      setUserStory('');
      setError(null);
    } catch (err) {
      setError('Failed to generate test case');
      console.error('Error generating test case:', err);
    } finally {
      setGenerating(false);
    }
  };

  const handleGenerateAutomation = async (id) => {
    try {
      const response = await testCaseService.generateAutomation(id);
      setTestCases(testCases.map(tc => 
        tc.id === id ? { ...tc, automationScript: response.data } : tc
      ));
    } catch (err) {
      setError('Failed to generate automation script');
      console.error('Error generating automation script:', err);
    }
  };

  // Select all handler
  const handleSelectAll = (e) => {
    if (e.target.checked) {
      setSelected(testCases.map(tc => tc.id));
    } else {
      setSelected([]);
    }
  };

  // Select one handler
  const handleSelectOne = (id) => {
    setSelected((prev) =>
      prev.includes(id) ? prev.filter(sid => sid !== id) : [...prev, id]
    );
  };

  // Delete selected handler
  const handleDeleteSelected = () => {
    setTestCases(testCases.filter(tc => !selected.includes(tc.id)));
    setSelected([]);
  };

  if (loading) {
    return (
      <div className="flex items-center justify-center h-64">
        <div className="animate-spin rounded-full h-12 w-12 border-b-2 border-primary-600"></div>
      </div>
    );
  }

  return (
    <div className="p-6">
      <div className="flex justify-between items-center mb-6">
        <h1 className="text-3xl font-bold">Activity Logs</h1>
        <div className="relative" ref={filterRef}>
          <button
            className="btn-primary flex items-center gap-2"
            onClick={() => setFilterOpen((open) => !open)}
            type="button"
          >
            <svg className="w-5 h-5" fill="none" stroke="currentColor" strokeWidth="2" viewBox="0 0 24 24"><path strokeLinecap="round" strokeLinejoin="round" d="M3 4a1 1 0 011-1h16a1 1 0 011 1v2a1 1 0 01-1 1H4a1 1 0 01-1-1V4zm0 7a1 1 0 011-1h16a1 1 0 011 1v2a1 1 0 01-1 1H4a1 1 0 01-1-1v-2zm0 7a1 1 0 011-1h16a1 1 0 011 1v2a1 1 0 01-1 1H4a1 1 0 01-1-1v-2z" /></svg>
            Filter
          </button>
          {filterOpen && (
            <div className="absolute right-0 mt-2 w-48 bg-white border rounded shadow-lg z-10 p-4">
              <label className="flex items-center mb-2">
                <input type="checkbox" checked={filters.testCase} onChange={() => handleFilterChange('testCase')} className="mr-2" /> Test Case
              </label>
              <label className="flex items-center mb-2">
                <input type="checkbox" checked={filters.bugs} onChange={() => handleFilterChange('bugs')} className="mr-2" /> Bugs
              </label>
              <label className="flex items-center mb-2">
                <input type="checkbox" checked={filters.userStory} onChange={() => handleFilterChange('userStory')} className="mr-2" /> User Story
              </label>
              <label className="flex items-center">
                <input type="checkbox" checked={filters.automation} onChange={() => handleFilterChange('automation')} className="mr-2" /> Automation
              </label>
            </div>
          )}
        </div>
      </div>
      {/* Select All and Delete Selected */}
      <div className="flex items-center mb-2">
        <input
          type="checkbox"
          className="mr-2"
          checked={selected.length === testCases.length && testCases.length > 0}
          onChange={handleSelectAll}
        />
        <span className="text-sm font-medium">Select All</span>
        {selected.length > 0 && (
          <button
            className="ml-4 px-4 py-1 rounded bg-red-500 text-white text-sm font-semibold hover:bg-red-600"
            onClick={handleDeleteSelected}
          >
            Delete Selected
          </button>
        )}
      </div>
      <div className="bg-white rounded-lg shadow overflow-hidden">
        <table className="min-w-full divide-y divide-gray-200">
          <thead className="bg-gray-50">
            <tr>
              <th className="px-4 py-3">
                {/* Empty for row checkboxes */}
              </th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Title
              </th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Status
              </th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Created Date
              </th>
              <th className="px-6 py-3 text-left text-xs font-medium text-gray-500 uppercase tracking-wider">
                Actions
              </th>
            </tr>
          </thead>
          <tbody className="bg-white divide-y divide-gray-200">
            {testCases.map(tc => (
              <tr key={tc.id}>
                <td className="px-4 py-4">
                  <input
                    type="checkbox"
                    checked={selected.includes(tc.id)}
                    onChange={() => handleSelectOne(tc.id)}
                  />
                </td>
                <td className="px-6 py-4 whitespace-nowrap">
                  <div className="text-sm font-medium text-gray-900">{tc.title || 'Test Case'}</div>
                  <div className="text-sm text-gray-500">{tc.description || 'Test case from user story'}</div>
                </td>
                <td className="px-6 py-4 whitespace-nowrap">
                  <span className="px-2 inline-flex text-xs leading-5 font-semibold rounded-full bg-green-100 text-green-800">
                    {tc.status || 'Passed'}
                  </span>
                </td>
                <td className="px-6 py-4 whitespace-nowrap text-sm text-gray-500">
                  {tc.createdDate || '2024-06-01'}
                </td>
                <td className="px-6 py-4 whitespace-nowrap text-sm font-medium">
                  <button className="text-primary-600 hover:text-primary-900 mr-4">Edit</button>
                  <button className="text-red-600 hover:text-red-900">Delete</button>
                </td>
              </tr>
            ))}
          </tbody>
        </table>
      </div>
    </div>
  );
};

export default ActivityLogs;