import React, { useState, useEffect, useRef } from 'react';
import { chatService } from '../services/api';
import { Link } from 'react-router-dom';

const mockChartData = [20, 40, 60, 80, 100];

const navItems = [
  { label: 'Dashboard', icon: (
    <svg className="w-6 h-6" fill="none" stroke="currentColor" strokeWidth="2" viewBox="0 0 24 24"><path strokeLinecap="round" strokeLinejoin="round" d="M3 12l2-2m0 0l7-7 7 7M13 5v6h6" /></svg>
  ), link: '/' },
  { label: 'Activity Logs', icon: (
    <svg className="w-6 h-6" fill="none" stroke="currentColor" strokeWidth="2" viewBox="0 0 24 24"><path strokeLinecap="round" strokeLinejoin="round" d="M9 17v-6a2 2 0 012-2h2a2 2 0 012 2v6m-6 0h6" /></svg>
  ), link: '/activitylogs' },
  { label: 'Settings', icon: (
    <svg className="w-6 h-6" fill="none" stroke="currentColor" strokeWidth="2" viewBox="0 0 24 24"><path strokeLinecap="round" strokeLinejoin="round" d="M12 4v16m8-8H4" /></svg>
  ), link: '/settings' },
];

function Dashboard() {
  // Chat state
  const [messages, setMessages] = useState([]);
  const [input, setInput] = useState('');
  const [loading, setLoading] = useState(false);
  const [chatOpen, setChatOpen] = useState(false);
  const audioRef = useRef(null);

  useEffect(() => {
    chatService.getHistory().then(res => setMessages(res.data)).catch(() => {});
  }, []);

  // Play sound when a new AI message arrives
  useEffect(() => {
    if (
      chatOpen &&
      messages.length > 0 &&
      messages[messages.length - 1].sender === 'ai'
    ) {
      console.log('Attempting to play bot reply sound');
      audioRef.current && audioRef.current.play().catch((e) => { console.log('Audio play error:', e); });
    }
  }, [messages, chatOpen]);

  const sendMessage = async (e) => {
    e.preventDefault();
    if (!input.trim()) return;
    setLoading(true);
    setMessages([...messages, { sender: 'user', message: input }]);
    try {
      const res = await chatService.sendMessage(input);
      setMessages([...messages, { sender: 'user', message: input }, { sender: 'ai', message: res.data.response }]);
    } catch {
      setMessages([...messages, { sender: 'user', message: input }, { sender: 'ai', message: 'Error from AI.' }]);
    }
    setInput('');
    setLoading(false);
  };

  const welcomeMessage = `👋 Welcome to Zap⚡️ - Your AI Testing Assistant!\n\nI can help you with:\n\n📝 Test Case Generation:\n   - Generate test cases from user stories\n   - link test cases to user stories\n   - automate test cases\n\n🤖 Automation Scripts:\n   - Create automation scripts from test cases or user stories\n   - upload script to project\n\n🐞 Bugs Creation:\n   - Create bug from user story\n   - link bug to user story\n\n🎑 Image analysis for UI element:\n   - UI element detection\n   - create test case for UI element\n   - create automation script for UI element\n\n🔍 How to get started:\n1. Use 'id ISSUE-ID' to work with a specific issue\n2. Type 'settings' to configure the application\n\nWhat would you like to do today? 🚀`;

  return (
    <div className="w-full bg-gradient-to-br from-blue-50 to-white min-h-screen">
      <main className="flex flex-col lg:flex-row">
        <section className="flex-1 p-10 flex flex-col justify-between">
          <div>
            <h1 className="text-4xl font-bold mb-2">Welcome!</h1>
            <p className="text-lg text-gray-600 mb-8">Welcome to AI-Powered QA Testing Test Tool Dashboard</p>
            {/* Bar Chart Mockup */}
            <div className="bg-white rounded-lg shadow p-6 mb-8">
              <div className="flex items-end h-40 space-x-6">
                {mockChartData.map((val, idx) => (
                  <div key={idx} className="flex flex-col items-center">
                    <div className="w-8 bg-gradient-to-t from-primary-400 to-primary-600 rounded-t-lg" style={{ height: `${val}px` }}></div>
                    <span className="mt-2 text-xs text-gray-500">{['Cases', 'Stories', 'Scripts', 'Bugs', 'Prod. QA'][idx]}</span>
                  </div>
                ))}
              </div>
            </div>
            {/* Quick Links */}
            <div className="grid grid-cols-1 md:grid-cols-3 gap-6">
              <div className="card text-center cursor-pointer hover:shadow-lg transition">
                <div className="text-primary-600 font-bold text-lg mb-2">AI Test</div>
                <div className="text-gray-500">Launch AI test tools</div>
              </div>
              <div className="card text-center cursor-pointer hover:shadow-lg transition">
                <div className="text-primary-600 font-bold text-lg mb-2">Automation Script</div>
                <div className="text-gray-500">Generate automation scripts</div>
              </div>
              <div className="card text-center cursor-pointer hover:shadow-lg transition">
                <div className="text-primary-600 font-bold text-lg mb-2">Bugs</div>
                <div className="text-gray-500">Track and manage bugs</div>
              </div>
            </div>
          </div>
        </section>
        {/* Chat Box */}
        {chatOpen ? (
          <aside className="w-full lg:w-96 bg-white rounded-l-3xl shadow-lg flex flex-col p-0 justify-between overflow-hidden">
            {/* Chat Header */}
            <div className="flex items-center gap-3 px-6 py-4 bg-gradient-to-r from-blue-100 to-blue-200 border-b">
              <div className="w-10 h-10 rounded-full bg-blue-300 flex items-center justify-center font-bold text-white text-lg overflow-hidden">
                <img src="/images/oldbot_avatar.png" alt="AI Avatar" className="w-full h-full object-cover" />
              </div>
              <div className="font-semibold text-lg text-gray-800">AI Agent</div>
              <div className="ml-auto flex gap-2 items-center">
                {/* Minimize button (horizontal line) */}
                <button className="w-8 h-8 rounded-full bg-white flex items-center justify-center shadow hover:bg-blue-50" onClick={() => setChatOpen(false)} title="Minimize">
                  <svg className="w-5 h-5 text-blue-500" fill="none" stroke="currentColor" strokeWidth="2" viewBox="0 0 24 24"><line x1="5" y1="12" x2="19" y2="12" strokeWidth="2" strokeLinecap="round" /></svg>
                </button>
               
              </div>
            </div>
            {/* Chat Messages */}
            <div className="flex-1 flex flex-col gap-2 px-4 py-4 h-80 overflow-y-auto bg-blue-50">
              {messages.length === 0 ? (
                <div className="bg-white rounded-xl p-4 text-gray-700 text-sm whitespace-pre-line shadow max-w-[90%] mx-auto">
                  {welcomeMessage}
                </div>
              ) : (
                messages.map((msg, idx) => (
                  <div key={idx} className={`flex ${msg.sender === 'user' ? 'justify-end' : 'justify-start'} items-end gap-2`}>
                    {msg.sender !== 'user' && (
                      <div className="w-8 h-8 rounded-full bg-blue-300 flex items-center justify-center font-bold text-white text-sm overflow-hidden">
                        <img src="/images/oldbot_avatar.png" alt="AI Avatar" className="w-full h-full object-cover" />
                      </div>
                    )}
                    <div className={`rounded-2xl px-4 py-2 text-sm shadow max-w-[70%] ${msg.sender === 'user' ? 'bg-green-400 text-white rounded-br-none' : 'bg-white text-gray-800 rounded-bl-none border'}`}>
                      {msg.message}
                    </div>
                    {msg.sender === 'user' && (
                      <div className="w-8 h-8 rounded-full bg-green-400 flex items-center justify-center font-bold text-white text-sm">U</div>
                    )}
                  </div>
                ))
              )}
              {loading && <div className="self-start text-xs text-gray-400">AI is typing...</div>}
            </div>
            {/* Compose Bar */}
            <form onSubmit={sendMessage} className="flex items-center gap-2 px-4 py-3 border-t bg-white">
              <button type="button" className="w-9 h-9 flex items-center justify-center rounded-full hover:bg-blue-50">
                <svg className="w-6 h-6 text-blue-500" fill="none" stroke="currentColor" strokeWidth="2" viewBox="0 0 24 24"><path strokeLinecap="round" strokeLinejoin="round" d="M15.172 7l-6.586 6.586a2 2 0 002.828 2.828l6.586-6.586a2 2 0 00-2.828-2.828z" /></svg>
              </button>
              <input
                className="input flex-1 rounded-full border-2 border-blue-200 focus:ring-2 focus:ring-blue-400"
                type="text"
                placeholder="Type your message..."
                value={input}
                onChange={e => setInput(e.target.value)}
                disabled={loading}
              />
              <button className="btn-primary rounded-full px-4 py-2" type="submit" disabled={loading || !input.trim()}>Send</button>
            </form>
            {/* Audio for bot message */}
            <audio ref={audioRef} preload="auto">
              <source src="/sounds/send.wav" type="audio/wav" />
              Your browser does not support the audio element.
            </audio>
          </aside>
        ) : (
          <button
            className="fixed bottom-8 right-8 z-50 w-16 h-16 rounded-full bg-blue-500 text-white flex items-center justify-center shadow-lg hover:bg-blue-600 transition"
            onClick={() => setChatOpen(true)}
            title="Open Chat"
          >
            {/* Maximize icon (square) */}
          
            <svg className="w-8 h-8" fill="none" stroke="currentColor" strokeWidth="2" viewBox="0 0 24 24"><rect x="5" y="5" width="14" height="14" rx="2" strokeWidth="2" strokeLinecap="round" /></svg>
           
          </button>
           
        )}

      </main>
    </div>
  );
}

export default Dashboard; 